@import "variables";
@import "mixin";

.header {
  margin: 0 auto;
  //max-width: 1600px;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* stylelint-disable */
  background: linear-gradient(
      120deg,
      rgba(86, 204, 242, 1) 0%,
      rgba(86, 204, 242, 1) 78%,
      rgba(252, 252, 253, 1) 68%,
      rgba(252, 252, 253, 1) 100%
  );
  /* stylelint-enable */
  height: $navHeight;
  box-sizing: border-box;
  position: relative;

  &__top {
    //@include flex-space-between;
    display: flex;
    justify-content: space-around;
    width: 100%;
    margin: 0 auto;
    padding: 48px 0 0;
  }

  &__logo {
    width: 53px;
    height: 32px;
  }

  &__button {
    transition: background-color 1s;
    margin-top: 42px;

    &--is-active {
      &:hover {
        background-color: $linkHover;
        color: $whiteColor;
      }
    }
  }

  &__slider {
    //background: url("https://banduraweb.github.io/layout_dia/images/slider/slide-img-1.jpg");
    //background-position: center;
    background-size: cover;
    height: 640px;
    //width: 1146px;
    margin-top: 80px;
    transition: background 1500ms ease;
    //position: relative;
  }

}

@media (min-width: 320px) and (max-width: 900px) {
  .header {
    height: fit-content;
    padding: 5px;
    background: rgba(86, 204, 242, 1);
    &__top {
      flex-direction: column;
      width: 100%;
      align-items: center;
    }
    &__heading {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
  }
}
